import React, {useEffect, useState} from "react";
import {Link, useParams} from "react-router-dom";
import ReadComponent from "./ReadComponent";
import { Typography, Paper, Box, Modal, Button , Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import axios from 'axios';


const initState = {
    id: 0,
    orderedProducts: '',
    totalPrice: '',
    orderType: '',
    totalAmount:'',
    manager: '',
}

const ReadPage = ({ id }) => {
  //const { id } = useParams(); // URL   ID .
  const [open, setOpen] = useState(false); //     .
    const [order, setOrder] = useState(initState);
    const handleOpen = () => setOpen(true); //     .
    const handleClose = () => setOpen(false); //     .

    const handleDelete = () => {
        orderDelete(id); //   
        handleClose(); //  
    };
    const orderAssign = async (id) => {
        try {
          await axios.get(`http://localhost:8080/api/orders/order/${id}`); //       .
          alert(' .');
        } catch (error) {
          console.error('   :', error);
        }
      };

    const orderComplete = async (id) => {
        try {
            await axios.get(`http://localhost:8080/api/orders/complete/${id}`); //       .
            alert(' .');
        } catch (error) {
            console.error('   :', error);
        }
    };


    useEffect(() => {
        // API    
        const fetchOrderDetails = async () => {
            try {
                const response = await axios.get(`http://localhost:8080/api/orders/${id}`);
                setOrder(response.data); //    
            } catch (error) {
                console.error('Error fetching the order details:', error);
            }
        };

        fetchOrderDetails(); // useEffect    
    }, [id]);

    const orderDelete = async (id) => {
        try {
            await axios.delete(`http://localhost:8080/api/orders/${id}`);
            alert(' .');
        } catch (error) {
            console.error('   :', error);
        }
    };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 6 }}>
      <Typography variant="h4" component="div" gutterBottom>
        Order Receipt
      </Typography>
      <Paper elevation={3} sx={{ p: 2, width: '500px', mt: 2 }}>
        <ReadComponent id={id} /> {/*  ID ReadComponent  prop . */}
      </Paper>
        {order.orderType === 'BEFORE_ORDER' && (
          <Box mt={2}> {/*     . */}
                  <Button sx={{ bgcolor: 'gray', color: 'white','&:hover': { bgcolor: 'gray' },mb : 4, mt : 2 , ml : 3 , mr : 3}}
                      variant="contained" color="primary" onClick={() => orderAssign(id)}>
                        
                  </Button>
                  <Button sx={{ bgcolor: 'gray', color: 'white','&:hover': { bgcolor: 'gray' },mb : 4, mt : 2, ml : 3 , mr : 3}}
                          variant="contained" color="primary" component={Link}
                          to="/order/modify">
                       
                  </Button>
              <Button
                  sx={{
                      bgcolor: 'gray',
                      color: 'white',
                      '&:hover': { bgcolor: 'gray' },
                      mb: 4,
                      mt: 2,
                      ml: 3,
                      mr: 3
                  }}
                  variant="contained"
                  color="primary"
                  onClick={handleOpen} //  
              >
                   
              </Button>

              {/*    */}
              <Dialog open={open} onClose={handleClose}>
                  <DialogTitle> </DialogTitle>
                  <DialogContent>
                      <DialogContentText>
                           ?
                      </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                      <Button onClick={handleDelete} color="primary" autoFocus>
                          
                      </Button>
                      <Button onClick={handleClose} color="primary">
                          
                      </Button>
                  </DialogActions>
              </Dialog>
                  </Box>
            )}
        {order.orderType === 'PROGRESS_ORDER' && (
            <Box mt={2}>
                <Button sx={{ bgcolor: 'gray', color: 'white','&:hover': { bgcolor: 'gray' },mb : 4, mt : 2 , ml : 3 , mr : 3}}
                        variant="contained" color="primary" onClick={() => orderComplete(id)}>
                     
                </Button>
                <Button sx={{ bgcolor: 'gray', color: 'white','&:hover': { bgcolor: 'gray' },mb : 4, mt : 2 , ml : 3 , mr : 3}}
                        variant="contained" color="primary">
                     
                </Button>
            </Box>
        )}
        {order.orderType === 'RETURNING' && (
            <Box mt={2}>
                <Button sx={{ bgcolor: 'gray', color: 'white','&:hover': { bgcolor: 'gray' },mb : 4, mt : 2 , ml : 3 , mr : 3}}
                        variant="contained" color="primary" onClick={() => orderComplete(id)}>
                     
                </Button>
            </Box>
        )}
    </Box>
  );
}

export default ReadPage;