const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(['width', 'margin'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
      '& .MuiDrawer-paper': {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
          easing: theme.transitions.easing.sharp,
          duration: theme.transitions.duration.enteringScreen,
        }),
        boxSizing: 'border-box',
        ...(!open && {
          overflowX: 'hidden',
          transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
          }),
          width: theme.spacing(7),
          [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
          },
        }),
      },
      '& .MuiDrawer-paper > .admin-profile': { //  
        display: open ? 'block' : 'none',
      },
      '& .MuiDrawer-paper > *': {
        overflowX: 'visible',
      },
    }),
);

// TODO remove, this demo shouldn't need to reset the theme.
const defaultTheme = createTheme();

export default function Dashboard() {
    //     0 :  / 1 :   / 2:
    const [statePage, setStatePage] = useState(0);



    const [user, setUser] = useState(null);
  const [open, setOpen] = React.useState(true);
  const [selectedUserId, setSelectedUserId] = useState(null);
  const { moveToRead } = useCustomMove(); // useCustomMove  moveToRead  .
  const handleRowClick = (userId) => {
    moveToRead(userId); // moveToRead  .
    console.log(selectedUserId);
  };
  const { userId } = useParams();

  const toggleDrawer = () => {
    setOpen(!open);
  };
    {/*   */}
    useEffect(() => {
        const fetchUser = async () => {
            try {
                const response = await axios.get('http://localhost:8080/api/user/current-user', {
                    withCredentials: true //     
                });
                console.log(response.data); //  
                setUser(response.data);
            } catch (error) {
                console.error("   :", error);
            }
        };

        fetchUser();
    }, []);

  {/*   */}
  const [alarmOpen, setAlarmOpen] = useState(false);
  const [notificationCount, setNotificationCount] = useState(0);
  const [messages, setMessages] = useState([]);

  useEffect(() => {
    const socket = new SockJS('http://localhost:8080/ws');
    const stompClient = Stomp.over(socket);

    stompClient.connect({}, function (frame) {
      console.log('Connected: ' + frame);

      stompClient.subscribe('/topic/notifications', function (message) {
        console.log('Received message:', message.body);
        setNotificationCount(prevCount => prevCount + 1);
        setMessages(prevMessages => [...prevMessages, message.body]); //  
      });
    });

    return () => {
      if (stompClient) {
        stompClient.disconnect();
      }
    };
  }, []);

  const handleAlarmOpen = () => {
    setAlarmOpen(true);
  };

  const handleAlarmClose = () => {
    setAlarmOpen(false);
    setNotificationCount(0); //     
    setMessages([]); //   ( )
  };


  {/**/}
  return (
      <>


        <ThemeProvider theme={defaultTheme}>
          <Box sx={{ display: 'flex' }}>
            <CssBaseline />
            <AppBar position="absolute" open={open}>
              <Toolbar
                  sx={{
                    pr: '24px', // keep right padding when drawer closed
                  }}
              >
                <IconButton
                    edge="start"
                    color="inherit"
                    aria-label="open drawer"
                    onClick={toggleDrawer}
                    sx={{
                      marginRight: '36px',
                      ...(open && { display: 'none' }),
                    }}
                >
                  <MenuIcon />
                </IconButton>
                  <Typography
                      component="h1"
                      variant="h6"
                      color="inherit"
                      noWrap
                      sx={{ flexGrow: 1, cursor: 'pointer' }} //    
                      onClick={() => setStatePage(0)} //    
                  >
                      Dashboard
                  </Typography>
                {/*  */}
                <IconButton color="inherit" onClick={handleAlarmOpen}>
                  <Badge badgeContent={notificationCount} color="secondary">
                    <NotificationsIcon />
                  </Badge>
                </IconButton>
                <Dialog
                    open={alarmOpen}
                    onClose={handleAlarmClose}
                    PaperProps={{
                      style: {
                        position: 'absolute',
                        top: '20px', //     
                        right: '1px', //   
                        transform: 'translateX(0)', //  
                      },
                    }}
                    BackdropProps={{ invisible: true }} //    
                >
                  <DialogTitle></DialogTitle>
                  <DialogContent>
                    {messages.length === 0 ? (
                        <Typography>  .</Typography>
                    ) : (
                        messages.map((msg, index) => (
                            <Typography key={index}>{msg}</Typography>
                        ))
                    )}
                  </DialogContent>
                  <DialogActions>
                    <Button onClick={handleAlarmClose}></Button>
                  </DialogActions>
                </Dialog>
                {/**/}
              </Toolbar>
            </AppBar>
            <Drawer variant="permanent" open={open}>
              <Toolbar
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-end',
                    px: [1],
                  }}
              >
              </Toolbar>
              <Divider />
              <List component="nav">
                  {/* */}
                  <MainListItems user={user} statePage={statePage} setStatePage={setStatePage} />
                <Divider sx={{ my: 1 }} />
              </List>
            </Drawer>
            <Box
                component="main"
                sx={{
                  backgroundColor: (theme) =>
                      theme.palette.mode === 'light'
                          ? theme.palette.grey[100]
                          : theme.palette.grey[900],
                  flexGrow: 1,
                  height: '100vh',
                  overflow: 'auto',
                }}
            >
              <Toolbar />
              <Container maxWidth="lg" sx={{ ml:4, mt: 4, mb: 4 }}>
                      {/*   */}
                    <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column',height : 'flex' , width: 1550 }}>
                        {/*statePage   */}
                        {statePage === 1 ? (
                            <OrderListPage/> // statePage 1 
                        ) : statePage === 2 ? (
                            <SupplierListPage/> // statePage 2 
                        )  : statePage === 3 ? (
                            <UserListPage/> // statePage 3 
                        ) : statePage === 5 ? (
                           <ProductListPage/> // statePage 4 
                        ) : ( // statePage 0 
                            <Explain/>
                  )}

              </Paper>
                {/*
                      <Routes>
                        <Route path="/add" element={<AddPage />} />
                        <Route path="/list" element={<OrderListPage />} />
                        <Route path="*" element={<Outlet />} />
                      </Routes>
                      */}
                <Copyright sx={{ pt: 4 }} />
              </Container>
            </Box>
          </Box>
        </ThemeProvider>
      </>
  );
}